// Determine if it is the current date and is not completed (based on the browser time zone)
export function isCurrentDayIncomplete(dateString: string) {
  if (!dateString) return false
  
  try {
    // Get the current date using the browser's time zone
    const now = new Date()
    const dataDate = new Date(dateString)
    
    // Get the date string (YYYY-MM-DD format) in the browser's time zone
    const getLocalDateString = (date: Date) => {
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      return `${year}-${month}-${day}`
    }
    
    const todayDateStr = getLocalDateString(now)
    const dataDateStr = getLocalDateString(dataDate)
    
    // If it is today, check whether it is not completed (the current time is less than 23:59:59 on the same day)
    if (todayDateStr === dataDateStr) {
      const endOfDay = new Date(now)
      endOfDay.setHours(23, 59, 59, 999)
      return now < endOfDay
    }
    
    return false
  } catch (error) {
    console.warn('Date parsing error:', error)
    return false
  }
}